@font-face {
  font-weight: 400;
  font-family: Jost;
  font-style: normal;
  src: url("../fonts/jost-regular.ttf");
  font-display: swap;
}
@font-face {
  font-weight: 700;
  font-family: Jost;
  font-style: bold;
  src: url("../fonts/jost-bold.ttf");
  font-display: swap;
}
@font-face {
  font-weight: 400;
  font-family: Forum;
  font-style: normal;
  src: url("../fonts/forum-regular.ttf");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
h1,
h2,
h3,
h4,
p,
fieldset,
ul {
  margin: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style-type: none;
}
button {
  display: inline-block;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  font: inherit;
  text-decoration: none;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
a {
  display: inline-block;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  font: inherit;
  text-decoration: none;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
.wrapper {
  box-sizing: border-box;
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .wrapper {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    max-width: 1280px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 32px;
    padding-left: 32px;
  }
}
.interactive-styles-links:hover {
  color: #bb8c5f;
}
.interactive-styles-links:focus-visible {
  color: #bb8c5f;
  outline: none;
}
.interactive-styles-links:active {
  color: #bb8c5f;
}
.app-btn {
  position: relative;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  border: 2px solid #bb8c5f;
}
.app-btn--dark {
  color: #faf6f2;
  background-color: #524336;
}
.app-btn--light {
  color: #bb8c5f;
  background-color: #eeeeee;
}
.app-btn--light:hover {
  color: #faf6f2;
  background-color: #bb8c5f;
}
.app-btn__corner {
  position: absolute;
  top: 50%;
  display: block;
  width: 17px;
  height: 19px;
}
.app-btn__corner::before,
.app-btn__corner::after {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 0;
  height: 0;
}
.app-btn__corner::before {
  border-top: 9.5px solid transparent;
  border-bottom: 9.5px solid transparent;
  border-right: 17px solid #bb8c5f;
}
.app-btn__corner::after {
  border-top: 7.5px solid transparent;
  border-bottom: 7.5px solid transparent;
  border-right: 13px solid transparent;
}
.app-btn__corner--left {
  left: -17px;
  transform: translateY(-50%);
}
.app-btn__corner--right {
  right: -17px;
  transform: translateY(-50%) rotate(180deg);
}
.app-btn--dark .app-btn__corner::after {
  border-right-color: #524336;
}
.app-btn--light .app-btn__corner::after {
  border-right-color: #eeeeee;
}
.app-btn--light:hover > .app-btn__corner::after {
  border-right-color: #bb8c5f;
}
.page {
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  font-size: 17px;
  color: #3e424b;
  font-family: "Jost", sans-serif;
  line-height: 24px;
  vertical-align: middle;
  background-color: #eeeeee;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}
.site-toggle {
  position: relative;
  padding-right: 38px;
  font-weight: 400;
  font-size: 25px;
  line-height: 35px;
}
.site-toggle--closed::after {
  position: absolute;
  right: 0;
  bottom: 1px;
  content: "";
  width: 28px;
  height: 28px;
  background-image: url("../../img/burger.svg");
  background-repeat: no-repeat;
  background-size: 24px 14px;
  background-position: center;
}
.site-toggle--opened {
  position: relative;
}
.site-toggle--opened::before,
.site-toggle--opened::after {
  position: absolute;
  top: 6px;
  right: 12px;
  width: 2px;
  height: 24px;
  margin: 0 auto;
  background-color: #bb8c5f;
  content: "";
}
.site-toggle--opened::before {
  transform: rotate(45deg);
}
.site-toggle--opened::after {
  transform: rotate(-45deg);
}
.site-toggle:hover {
  color: #bb8c5f;
}
.site-toggle:focus-visible {
  color: #bb8c5f;
  outline: none;
}
.site-toggle:active {
  color: #bb8c5f;
}
