  background-color: #050402;
}
.intro-slider__arrow {
  position: relative;
  width: 40px;
  height: 90px;
}
.intro-slider__arrow::before {
  position: absolute;
  transform: translate(-50%);
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 1.5px solid #bb8c5f;
}
.intro-slider__arrow--prev::before {
  bottom: 0;
}
.intro-slider__arrow--next::before {
  top: 0;
}
.intro-slider__arrow::after {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  width: 10px;
  height: 41px;
  margin: 0 auto;
  background-image: url("../../img/line-36.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
}
.intro-slider__arrow--prev::after {
  bottom: 22px;
}
.intro-slider__arrow--next::after {
  top: 22px;
  transform: rotate(180deg);
}
.intro-slider__arrow:hover::after {
  width: 10px;
  height: 69px;
  background-image: url("../../img/line-64.svg");
}
.intro-slider__arrow--prev:hover::after {
  transform: rotate(180deg);
}
.intro-slider__arrow--next:hover::after {
  transform: rotate(0);
}
.intro-slider__pagination-item {
