  width: 11px;
  height: 29px;
}
.card-mark__corner::before,
.card-mark__corner::after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
}
.card-mark__corner::before {
  right: 0;
  border-top: 14.5px solid transparent;
  border-bottom: 14.5px solid transparent;
